@use 'base/variables' as v;
@use 'base/mixins' as m;

.lineup{
    background-color: v.$rosa;
    padding: 5rem 0;
    h3, p{
        text-align: center;
        color: v.$blanco;
    }
    .dia{
        text-transform: uppercase;
        margin: 5rem 0;
    }
}

.escenario-contenedor{
     @include m.tablet { 
        @include m.grid(2,5rem)
     }
     p{
        color: v.$negro;
     }
     .escenario{
        padding-top: 3rem;
        p{
            &::after{
                content: '';
                display: block;
                width: 20rem;
                height: 20rem;
                background-size: 15rem;
                background-repeat: no-repeat;
                background-position: center center;
                margin: 2rem auto 0 auto;
            }
        }
     }
}

.edm .nombre-escenario:after{
     background-image: url(/src/img/icono_edm.png);
}
.rock .nombre-escenario:after{
    background-image: url(/src/img/icono_rock.png);
}

.calendario{
    @include m.resetear-lista;
    li{
        padding: 2.5rem;
        font-weight: 700;
        span{
            font-weight: 400;
        }
    }
}

.bg-amarillo{
    background-color: v.$amarillo;
    li:nth-child(odd){
        background-color: darken(v.$amarillo, 15%);
    }
}
.bg-verde{
    background-color: v.$verde;
    li:nth-child(odd){
        background-color: darken(v.$verde, 10%);
    }
}